.Chat {
  overflow-x: hidden;
  overflow-y: auto;
  background-color: whitesmoke;
}

.messages {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 100%;
}
.messages > li {
  padding: 0.5rem 1rem;
  background: rgba(116, 244, 159, 0.329);
}
.messages > li:nth-child(odd) {
  background: rgb(255, 255, 255);
}
